<script>
  import './orders.less'

  import game_seetings from '@/utils/game_settings';

  export default {

    data(){
      return{
        showAllOrders: true,
        showYourOrders: false,
      }
    },

    props: ['orders', 'yourorders'],

    methods: {
      dateFormat(raw) {
        return new Date(raw+'Z').toLocaleString();
      },

      showAll(){
        this.showAllOrders = true;
        this.showYourOrders = false;
      },

      showYours(){
        this.showAllOrders = false;
        this.showYourOrders = true;
      }
    }
  };
</script>

