<template>
  <main id="app">
    <dice-header />
    <dice-game :orders="apporders"></dice-game>
    <!-- <dice-orders /> -->
    <dice-orders :orders="apporders" :yourorders="yourorders"></dice-orders>
  </main>
</template>

<script>
  // import fetch from '@/utils/api';
  const axios = require('axios');
  /* global scatter */
  const task = new Promise(r => {
    document.addEventListener('scatterLoaded', r);
  });

  export default {
    mounted() {
      task.then(() => {
        if (!scatter.identity) return;
        const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
        if (!account) return;
        this.$store.commit('UPDATE_ACCOUNT', account);
      });
      this.initOrders();
      this.fetchOrders();
    },

    components: {
      diceHeader: require('@/components/header').default,
      diceGame: require('@/components/game').default,
      diceOrders: require('@/components/orders').default
    },

    data() {
      return {
        // apporder:{},
        apporders: [],
        yourorders: [],
      }
    },

    methods: {
      fetchOrders() {
        // return;

        Pusher.logToConsole = true;

        var pusher = new Pusher('df97ed45f323bb2d64d5', {
          cluster: 'ap1',
          forceTLS: true
        });

        var channel = pusher.subscribe('my-channel');
        channel.bind('my-event', (data)=>{
          // alert(data);

          var edge = 200;
          if(data.referral !== 'eoslotcasino'){
            edge = 150;
          }

          var payout = '0.0000 EOS';
          if(data.roll_under > data.random_roll){
            payout = data.bet_amt * ((10000-edge)*100/(data.roll_under - 1)) /100000000;
            payout = (new Number(payout).toFixed(4)).toString() + " EOS";;
          }

          var bet_amt = (new Number(data.bet_amt / 10000).toFixed(4)).toString() + " EOS";

          data.bet_amt = bet_amt;
          data.payout = payout;
          this.apporders.splice(0, 0, data);

          if(this.$store.state.account){
            if(this.$store.state.account.name == data.bettor){
              this.yourorders.splice(0,0,data);
            }
          }
        });

      },

      initOrders(){
        axios.get('//api.eoslot.me/bets')
      .then((response) => {
        // handle success
        for(var i=0;response.data.length;i++){
          var tmp = response.data[i];

          var edge = 200;
          if(tmp.referral !== 'eoslotcasino'){
            edge = 150;
          }

          var payout = '0.0000 EOS';
          if(tmp.roll_under > tmp.random_roll){
            payout = tmp.bet_amt * ((10000-edge)*100/(tmp.roll_under - 1)) /100000000;
            payout = (new Number(payout).toFixed(4)).toString() + " EOS";;
          }

          var bet_amt = (new Number(tmp.bet_amt / 10000).toFixed(4)).toString() + " EOS";

          tmp.bet_amt = bet_amt;
          tmp.payout = payout;
          this.apporders.push(tmp);
        }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
      },
    }
  };
</script>

<style>
#app {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}
</style>
