<template>
    <header class="header">
        <div>
            <img
                    class="dice-logo"
                    :src="diceLogo"/>
        </div>


        <nav>
            <ul>
                <li>
                    <ul class="new-lang">
                        <p v-on:click = "changeLang('en','English')" class="f32">
                            <span class="flag gb"></span>
                        </p>
                        <p v-on:click = "changeLang('zh', '中文')" class="f32">
                            <span class="flag cn"></span>
                        </p>
                        <p v-on:click = "changeLang('ja', 'Japanese')" class="f32">
                            <span class="flag jp"></span>
                        </p>
                    </ul>
                </li>
                <li>
                    <a
                            @click="showReferral"
                            href="JavaScript:;">{{ $t("header['referrals']") }}</a>
                </li>
                <li>
                    <a
                            @click="showAbout"
                            href="JavaScript:;">{{ $t("header['how']") }}</a>
                </li>
                <li>
                    <a
                            @click="login"
                            v-if="!account.name"
                            href="JavaScript:;">{{ $t("header['login']") }}</a>
                    <div
                            class="account-cell"
                            v-else
                            href="JavaScript:;">
                        <span>{{account.name}}</span>
                        <font-awesome-icon
                                class="icon-logout"
                                @click="logout"
                                icon="sign-out-alt"/>
                    </div>
                </li>
            </ul>
        </nav>
    </header>
</template>

<script>
    import network from '@/utils/network';
    import eventHub from '@/utils/event';
    import diceLogo from '@/assets/eoslot_logo.png';
    import './header.less'
    import './animated.less'

    export default {
        methods: {
            login() {
                scatter.getIdentity({
                    accounts: [network]
                }).then(() => {
                    const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
                    if (!account) return;
                    this.$store.commit('UPDATE_ACCOUNT', account);
                }).catch(e => {
                    this.$message.warning(e.message);
                });
            },

            logout() {
                scatter.forgetIdentity().then(() => {
                    this.$message.success('User logout success');
                    this.$store.commit('UPDATE_ACCOUNT', {});
                });
            },

            toggleLanguage() {
                this.$i18n.locale = 'en';
            },

            showAbout() {
                eventHub.$emit('SHOW_ABOUT');
            },

            showReferral() {
                eventHub.$emit('SHOW_REFERRALS');
            },

            showSocial() {
                eventHub.$emit('SHOW_SOCIAL');
            },

            changeLang(lang, langStr) {
                this.$i18n.locale = lang;
                this.lang = langStr
            }

        },

        data() {
            return {
                diceLogo: diceLogo,
                lang: '中文'
            };
        },

        computed: {
            account() {
                return this.$store.state.account;
            }
        }
    };
</script>

